<template>
	<view class="predict">
		<view style="height: 90rpx;">
			<view class="toptitle">
				<text>{{predictName||'未填写'}}</text>
			</view>
		</view>
		<!-- banner图 -->
		<swiper v-if="casePredictbanner.length!=0" :indicator-dots="true" :autoplay="Imageplay" :interval="3000" :duration="1000" indicator-color="#333333" indicator-active-color="#ffffff" circular="true">
			<swiper-item  @tap='PreviewPic(item,casePredictbanner)' v-for="(item,index) in casePredictbanner" :key='index'>
				<view class="swiper-item">
					<image :src='item.fileDownloadUrl' mode="aspectFit"></image>
				</view>
			</swiper-item>	
		</swiper>
		<swiper v-else :indicator-dots="true" :autoplay="Imageplay" :interval="3000" :duration="1000" indicator-color="#333333" indicator-active-color="#ffffff" circular="true">
			<swiper-item>
				<view class="swiper-item">
					<image src='../../static/image/default.png' mode="aspectFit"></image>
				</view>
			</swiper-item>	
		</swiper>
		<!-- 医生信息 -->
		<view class="docInfor" v-for="(item,index) in docinfor" :key='item.caseHistoryId'>
			<image :src='item.doctorVO.avatarLinks' mode=""></image>
			<view class="docInfor_content">
				<view class="">
					<text class="docname">{{item.doctorVO.name||'暂无数据'}}</text>
					<view class="infor">
						<text>{{item.doctorVO.department||'暂无数据'}}</text>
						<text class="line">|</text>
						<text>{{item.doctorVO.hospital||'暂无数据'}}</text>
					</view>
				</view>
				<view class="infor">
					<!-- <text>{{caseDesign.createTime}}</text> -->
				</view>
			</view>
		</view>
		<!-- 术后随访数据 -->
		<view class="follow" v-for="(item,index) in predicted" :key='index'>
			<view class="title">
				<text>效果预测</text>
				<text>{{index+1}}</text>
			</view>
			<view class="content">
				<text>{{item.name||'暂无数据'}}</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				Imageplay:true
			}
		},
		props:{
			predicted:{
				type:Array,
				default:()=>{
					return[]
				}
			},
			docinfor:{
				type:Array,
				default:()=>{
					return[]
				},
			},
			casePredictbanner:{
				type:Array,
				default:()=>{
					return[]
				},
			},
			predictName:{
				type:String,
				default:'',
			},
		},
		created(){
			console.log(this.predicted)
		}
	}
</script>

<style scoped>
	.toptitle{
		padding: 26rpx 0;
		background-color: #FFFFFF;
		text-align: center;
		position: fixed;
		width: 100%;
		z-index: 100;
		color: #000000;
		font-family: PingFang-SC-Medium;
		border-top: 1px solid #DEDEDE;
		border-bottom: 1px solid #DEDEDE;
		font-weight: 500;
	}
	.swiper-item image{
		width: 100%;
		height: 420rpx;
		background: #FFFFFF;
	}
	uni-swiper{
		height: 420rpx!important;
	}
	.docInfor{
		display: flex;
		padding: 30rpx 30rpx;
		align-items: flex-start;
		border-bottom: 1px solid #F0EFEF;
		background-color: #FFFFFF;
	}
	.docInfor uni-image{
		width: 100rpx;
		height: 100rpx;
		border-radius: 50%;
		border: 1px solid #F0EFEF;
		margin-right: 23rpx;
	
	}
	.docInfor .docname{
		font-size: 28rpx;
		color: #000000;
	}
	.docInfor_content{
		flex: 1;display: flex;justify-content: space-between;
	}
	.infor .line{
		margin: 0 10rpx;
	}
	.docInfor .infor{
		font-size: 24rpx;
		line-height: 70rpx;
		color: #8F8F8F;
	}
	.follow{
		margin-top: 20rpx;
		background-color: #FFFFFF;
		padding: 0 30rpx;
		
	}
	.title{
		color: #242424;
		font-size: 28rpx;
		font-weight: Bold;
		font-family: PingFang-SC-Bold;
		padding: 20rpx 0;
		border-bottom: 1px solid #F0F0F0;
	}
	.content{
		color: #000000;
		font-size: 28rpx;
		padding: 20rpx 0;
		font-family: PingFang-SC-Regular;
	}
</style>
